import ElSTable from './ElSTable'

const components = [ElSTable]

const install = Vue => {
  // 
  if (install.installed) return
  install.installed = true
  // 
  components.map(component => Vue.use(component))
}

// vue
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  // installVue.use()
  install,
  ...components,
}

